export const times = ['09:00', '09:30', '10:00', '10:30', '11:00', '11:30', '12:00', '12:30', '13:00', '13:30', '14:00', '14:30', '15:00', '15:30', '16:00', '16:30', '17:00', '17:30', '18:00', '18:30', '19:00', '19:30', '20:00', '20:30', '21:00', '21:30', '22:00', '22:30', '23:00']

export const findNextTime = function (time) {
  let index = times.indexOf(time)
  if (index >= 0 && index < times.length - 1) {
    return times[index + 1]
  }
}

export const calPeriod = function (times) {
  let periods = [[times[0]]]
  times.reduce((pre, cur) => {
    if (findNextTime(pre) === cur) {
      periods[periods.length - 1].push(cur)
    } else {
      periods.push([cur])
    }
    return cur
  })
  periods = periods.map(item => {
    item = `${item[0]}-${findNextTime(item[item.length - 1])}`
    return item
  })
  return periods.join(' ')
}
