import { calPeriod } from '../utils/times'
export default {
  data () {
    return {
      subjectMaps: {
        read: '阅读',
        hearing: '听力',
        writing: '写作',
        speaking: '口语'
      },
      studentsData: []
    }
  },
  created () {
    this.$indexedDB.initStore('students', this.queryStudents)
  },
  methods: {
    queryStudents () {
      this.$indexedDB.http({
        method: 'get',
        url: 'students'
      }).then((res) => {
        this.studentsData = res.data
        for (const student of res.data) {
          if (!student.isDelect) {
            this.$set(this.studentsMap, student.id, student.name)
          }
        }
      })
    },
    ratherDate (date, date2) {
      let today = this.$lib.myMoment(date2 || Date.now()).formate('YYYY-MM-DD')
      return new Date(today) > new Date(date)
    },
    dealAllData (dealData) {
      let datas = []
      for (const table of dealData) {
        let time = ''
        for (const data of datas) {
          if (data.studentId === table.studentId && data.date === table.date) {
            time = table.time
            data.times.push(time)
            data.count += 0.5
            data.times = data.times.sort((x, y) => {
              return new Date(table.date + ' ' + x).getTime() - new Date(table.date + ' ' + y).getTime()
            })
          }
        }
        if (!time) {
          let t = { ...table }
          t.times = [table.time]
          t.count = 0.5
          datas.push(t)
        }
      }
      datas = datas.sort((x, y) => {
        return new Date(x.date).getTime() - new Date(y.date).getTime()
      })
      datas = datas.map((item, i) => {
        item.periods = calPeriod(item.times)
        return item
      })
      return datas
    }
  }
}
